<template>
  <div class="baby-list-container">
    
    <div @click="$router.push( '/my/my-baby/edit' )" class="baby-list f-row-between-c">
      <div :class="['avatar', 'f-c', 'avatar-girl']">
        <img src="@/assets/images/baby_boy.png" alt="">
        <!-- <img src="@/assets/images/baby_boy.png" alt=""> -->
      </div>
      <div class="content f-row-between-c">
        <div class="name-and-sex f-i-c">
          <div class="name one-ellipsis">
            晨晨
          </div>
          <span class="sex">（女生）</span>
        </div>
        <div class="age-and-icon f-row-between-c">
          <p>
            4岁
          </p>
          <img src="@/assets/images/my_more.png" alt="">
        </div>
      </div>
    </div>

    <div @click="$router.push( '/my/my-baby/add' )" class="baby-add f-c">
      <img src="@/assets/images/add_baby.png" alt="">
      <p>
        添加宝宝
      </p>
    </div>

  </div>
</template>

<script>
export default {
  created() {
    document.title = '宝宝信息';
  }
}
</script>

<style lang="scss" scoped>
.baby-list-container {
  width: 100%;
  min-height: 100%;
  background: #fff;
  padding: 20px 16px 0;
  box-sizing: border-box;

  .baby-list, .baby-add {
    width: 100%;
    height: 80px;
    background: rgba(255,255,255,1);
    box-shadow: 0px 2px 8px 0px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    margin-bottom: 20px;
    padding: 10px 15px;
    box-sizing: border-box;

    .avatar {
      width: 60px;
      height: 60px;
      border-radius: 50%;
      
      img {
        width: 40px;
        height: 33px;
      }
    }
    .avatar-boy {
      background-color: #D0ECFC;
    }
    .avatar-girl {
      background-color: #FFDEE3;
    }

    .content {
      width: calc(100% - 75px);

      .name-and-sex {
        width: 68%;
        
        .name {
          max-width: 55%;
          font-size: 16px;
          font-weight: 600;
          color: rgba(51,51,51,1);
        }

        .sex {
          width: 45%;
          font-size: 15px;
          color: rgba(153,153,153,1);
        }
      }

      .age-and-icon {
        width: 30%;

        p {
          width: calc(100% - 20px);
          font-size: 15px;
          color: rgba(51,51,51,1);
          text-align: right;
        }
        img {
          width: 7px;
          height: 12px;
        }
      }
    }
  }

  .baby-add {
    
    img {
      width: 15px;
      height: 15px;
    }
    p {
      font-size: 16px;
      font-weight: 600;
      color: rgba(244,186,65,1);
      margin-left: 10px;
    }
  }
}
</style>


